  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ConvenioCalusacdCreatePage', { record : item,op:this.navParams.data.record.tipo,idgrupo:this.navParams.data.idgrupo,idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
